
.card {
  display: flex;
  width: 300px;
  margin: 10px 70px;
  border-radius: 2px 20px;
  box-shadow: 3px 3px 3px 3px rgba(0, 0, 0, 0.5);
 
}
.container__card {
  max-width: 1200px;
  margin: 2px;
  margin-top: 2px;
  display: flex;
  flex-direction:column;
 
}
img {
  width: 6rem;
  height: 100px;
  padding: 5px;
}
h3 {
  text-transform: uppercase;
  font-family: "Times New Roman", Times, serif;
}
